.footer_section {
  border-top: 1px solid #97d28b;
  padding: 24px 0;

  @media screen and (min-width: 768px) {
    padding: 40px 0;
  }
}

.footer_linksBox {
  display: grid;
  grid-template-columns: 5fr 1fr;
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 24px;

  @media screen and (min-width: 768px) {
    grid-template-columns: 2fr repeat(2, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
  }
}

.footer_logo {
  grid-area: 1 / 1 / 2 / 2;
  justify-self: start;
}

.arrowBtn {
  grid-area: 1 / 2 / 2 / 3;
  background-color: #97d28b;
  border-radius: 100%;
  width: 32px;
  height: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  justify-self: end;

  @media screen and (min-width: 768px) {
    grid-area: 1 / 3 / 2 / 4;
  }
}

.arrowBtn:hover {
  background-color: #173d33;
}

.arrowBtn:hover > * {
  fill: #97d28b;
}

.footer_socials {
  cursor: pointer;
  grid-area: 2 / 1 / 3 / 3;
  justify-self: center;
  align-self: center;

  @media screen and (min-width: 768px) {
    grid-area: 1 / 2 / 2 / 3;
    justify-self: start;
  }
}

.footer_contactsInfo {
  padding: 24px 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  letter-spacing: -0.04em;
  color: #173d33;

  @media screen and (min-width: 768px) {
    padding: 16px 0 0;
    flex-direction: row;
  }

  @media screen and (min-width: 1280px) {
    padding: 12px 0 0;
  }
}

.footer_contacts {
  font-style: normal;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    align-items: center;
  }
  @media screen and (min-width: 1280px) {
    justify-content: start;
  }
}

.footer_adress {
  margin-bottom: 16px;

  @media screen and (min-width: 768px) {
    margin-right: 67px;
    margin-bottom: 0;
  }

  @media screen and (min-width: 1280px) {
    margin-right: 332px;
  }
}

.footer_copyright {
  margin-top: 16px;

  @media screen and (min-width: 768px) {
    margin-left: 50px;
    margin-top: 0;
  }

  @media screen and (min-width: 1280px) {
    margin-left: 167px;
  }
}

.social_icon:hover {
  fill: #97d28b;
}

.social_icon:last-child {
  margin-left: 8px;
}
