.FAQ_section {
  padding: 36px 0;

  @media screen and (min-width: 768px) {
    padding: 100px 0 0;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 24px;
    grid-row-gap: 0px;
  }
  @media screen and (min-width: 1280px) {
    padding: 120px 0 0;
    grid-column-gap: 185px;
  }
}

.FAQ_title {
  font-family: "Oswald_Regular";
  font-size: 28px;
  font-weight: 400;
  line-height: 1;
  color: #173d33;
  text-transform: uppercase;
  margin: 0 auto 24px;

  @media screen and (min-width: 768px) {
    font-size: 36px;
    margin: 0 auto;
    grid-area: 1 / 2 / 2 / 3;
  }

  @media screen and (min-width: 1280px) {
    font-size: 48px;
    width: 398px;
  }
}

.FAQ_question,
.FAQ_noAnswerText {
  color: #173d33;
  text-align: justify;
  letter-spacing: -0.04em;
  font-size: 18px;

  @media screen and (min-width: 1280px) {
    font-size: 24px;
  }
}

.FAQ_icon {
  content: "\02795";
  padding: 4px;
  font-size: 16px;
  color: #97d28b;
  float: left;
  margin-right: 8px;
  display: block;

  @media screen and (min-width: 768px) {
    margin-right: 16px;
    font-size: 28px;
    padding: 7px;
  }

  @media screen and (min-width: 1280px) {
    margin-right: 24px;
  }
}

.active::before {
  content: "\2796";
  color: #173d33;
  margin-right: 8px;
  display: block;

  @media screen and (min-width: 768px) {
    margin-right: 16px;
    font-size: 28px;
  }

  @media screen and (min-width: 1280px) {
    margin-right: 24px;
  }
}

.FAQ_noAnswerText {
  margin-bottom: 12px;
}

.FAQ_list {
  @media screen and (min-width: 768px) {
    grid-area: 1 / 1 / 3 / 2;
  }
}

.FAQ_questionBox {
  border-top: 1px solid #97d28b;
  display: flex;
  align-items: center;
  padding: 16px 0;

  @media screen and (min-width: 1280px) {
    padding: 24px 0;
  }
}

.FAQ_contactBlock {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 36px;

  @media screen and (min-width: 768px) {
    padding-top: 0;
    grid-area: 2 / 2 / 3 / 3;
    align-self: flex-end;
  }
}

.FAQ_answerText {
  color: #173d33;
  text-align: justify;
  letter-spacing: -0.04em;
  font-size: 14px;
  padding-left: 24px;

  @media screen and (min-width: 768px) {
    padding-left: 44px;
  }

  @media screen and (min-width: 1280px) {
    padding-left: 52px;
    font-size: 16px;
  }
}
