.values_list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 24px;
  grid-row-gap: 24px;

  @media screen and (min-width: 768px) {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
  }
  @media screen and (min-width: 1280px) {
    grid-column-gap: 24px;
    grid-row-gap: 24px;
  }
}

.values_item:not(:nth-child(5n), :nth-child(6n)) {
  background-color: #eaedf1;
  padding: 12px;

  @media screen and (min-width: 1280px) {
    padding: 48px 24px;
  }
}

.values_item:nth-child(1n) {
  grid-area: 1 / 1 / 2 / 2;
}
.values_item:nth-child(2n) {
  grid-area: 1 / 2 / 2 / 3;
}

.values_item:nth-child(3n) {
  grid-area: 2 / 1 / 3 / 2;

  @media screen and (min-width: 768px) {
    grid-area: 2 / 3 / 3 / 4;
  }
}

.values_item:nth-child(4n) {
  grid-area: 2 / 2 / 3 / 3;

  @media screen and (min-width: 768px) {
    grid-area: 2 / 4 / 3 / 5;
  }
}

.values_item:nth-child(5n) {
  grid-area: 1 / 3 / 2 / 5;
}

.values_item:nth-child(6n) {
  grid-area: 2 / 1 / 3 / 3;
}

.values_item:nth-child(5n) img,
.values_item:nth-child(6n) img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.values_section {
  padding: 36px 0 0;

  @media screen and (min-width: 768px) {
    padding: 100px 0 0;
  }

  @media screen and (min-width: 1280px) {
    padding: 120px 0 0;
  }
}

.values_title {
  font-family: "Oswald_Regular";
  font-size: 28px;
  font-weight: 400;
  line-height: 1;
  color: #173d33;
  text-transform: uppercase;

  @media screen and (min-width: 768px) {
    font-size: 36px;
    width: 272px;
    flex: 1;
    flex-shrink: 0;
  }

  @media screen and (min-width: 1280px) {
    font-size: 48px;
    padding-right: 170px;
    flex-shrink: 0;
    flex-basis: 0;
  }
}

.values_text {
  color: #173d33;
  margin-bottom: 24px;
  text-align: justify;
  letter-spacing: -0.04em;
  margin-top: 24px;

  @media screen and (min-width: 768px) {
    flex: 1;
    margin-top: 0;
    padding-left: 10px;
    border-left: 1px solid #97d28b;
    width: 342px;
  }

  @media screen and (min-width: 1280px) {
    margin-top: 0;
    padding-left: 170px;
    width: 459px;
    flex-shrink: 0;
    flex-basis: 0;
  }
}

.values_titleBlock {
  display: flex;
  flex-direction: column;
  padding-bottom: 36px;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    justify-content: space-between;
    padding-bottom: 100px;
  }

  @media screen and (min-width: 1280px) {
    padding-bottom: 120px;
  }
}

.values_itemTitle {
  font-size: 16px;
  font-family: "Oswald_Regular";
  font-weight: 400;
  text-transform: uppercase;
  line-height: 1;
  color: #173d33;
  margin-left: 8px;

  @media screen and (min-width: 768px) {
    font-size: 18px;
  }

  @media screen and (min-width: 1280px) {
    font-size: 32px;
  }
}

.values_itemHeader {
  display: flex;
  align-items: center;

  padding-bottom: 33px;
  border-bottom: 1px solid #97d28b;

  @media screen and (min-width: 768px) {
    padding-bottom: 51px;
  }

  @media screen and (min-width: 1280px) {
    padding-bottom: 94px;
  }
}

.values_itemText {
  color: #173d33;
  font-size: 14px;
  text-align: justify;
  letter-spacing: -0.04em;
  padding-top: 12px;

  @media screen and (min-width: 1280px) {
    padding-bottom: 94px;
    font-size: 16px;
    padding-top: 24px;
  }
}

.values_icon {
  fill: #173d33;
  width: 16px;
  height: 16px;
  @media screen and (min-width: 1280px) {
    width: 24px;
    height: 24px;
  }
}
