.main_section {
  padding-top: 146px;

  @media screen and (min-width: 768px) {
    padding-top: 164px;
  }

  @media screen and (min-width: 1280x) {
    padding-top: 200px;
  }
}

.main_title {
  font-family: "Oswald_Regular";
  font-size: 36px;
  font-weight: 400;
  color: #173d33;
  text-transform: uppercase;
  margin-bottom: 24px;
  line-height: 1;

  @media screen and (min-width: 768px) {
    font-size: 48px;
    width: 300px;
    margin-right: 65px;
    margin-bottom: 0;
  }

  @media screen and (min-width: 1280px) {
    font-size: 64px;
    width: 485px;
    margin-right: 296px;
  }
}

.main_textBlock {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.main_text {
  color: #173d33;
  margin-bottom: 24px;
  text-align: justify;
  letter-spacing: -0.04em;

  @media screen and (min-width: 768px) {
    width: 342px;
    margin-bottom: 43px;
  }

  @media screen and (min-width: 1280px) {
    width: 364px;
    margin-bottom: 20px;
  }
}

.main_titleBlock {
  display: flex;
  flex-direction: column;
  padding-bottom: 24px;
  border-bottom: 1px solid #97d28b;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    align-items: start;
  }
}

.main_contactsInfo {
  padding: 24px 0 36px 0;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  letter-spacing: -0.04em;
  color: #173d33;

  @media screen and (min-width: 768px) {
    padding: 16px 0 40px 0;
  }

  @media screen and (min-width: 1280px) {
    padding: 12px 0 36px 0;
  }
}

.main_contacts {
  font-style: normal;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    align-items: center;
  }
}

.main_adress {
  margin-bottom: 8px;

  @media screen and (min-width: 768px) {
    margin-right: 77px;
    margin-bottom: 0;
  }

  @media screen and (min-width: 1280px) {
    margin-right: 492px;
  }
}

.main_copyright {
  @media screen and (min-width: 768px) {
    margin-left: 50px;
  }

  @media screen and (min-width: 1280px) {
    margin-left: 167px;
  }
}
